@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main-header{
    display: flex;
    background-color: rgba(0, 0, 0, 0.261);
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    min-height: 10vh;
   position:-webkit-sticky;
   position:sticky;
}
.navbar{
    margin: 2rem;
}
.navbar__li , a{
    font-size: 2.5rem;
    list-style: none;
    color: white;
}
.navbar__li a{
    text-decoration: none;
    border-bottom: 1px solid rgb(247, 169, 0);
}
.recuadro img{
    min-width: 25rem;
    min-height: 25rem;
    max-width: 25rem;
    max-height: 25rem;
}
.container, .container__technologies{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    max-width: 70vw;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
}

.recuadro{
    margin: 2rem;
    color: white;
    font-size: 2.3rem;
}
.range{
    margin-top: 2rem;
    height: 20px;
    width: 100%;
    background-color: #f1f1f1;
    border-radius: 50px;
    -webkit-appearance: none;
    }
.range:disabled{
    color: red;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
  background-image: linear-gradient( 94.3deg,  rgba(26,33,64,1) 10.9%, rgba(81,84,115,1) 87.1% );
  height: 100vh;
}
.banner{
  position:absolute;
  width: 100vw;
  min-height: 50vh;
  min-height: 50vh;
  -webkit-columns: 1fr 1fr;
          columns: 1fr 1fr;
  border-color: red;
  background-color: white;
  -webkit-clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
          clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
  background-image: url(https://i.postimg.cc/fRTrzh6r/banner.png);
  background-repeat: round;
  z-index: -1;
}
.my-name{
  position:absolute;
  color: rgb(247, 169, 0);
  font-size: 7rem;
  padding-left: 9rem;
  z-index: 1;
  font-family: 'IBM Plex Serif', serif;
}
.separador{
  width: 100%;
  height: 50vh;
}
.profile__img{
  z-index: 1;
  float: right;
  border-radius:50%;
  margin: 3rem;
  width: 9vw;
}
.container__h1{
  color: white;
  font-size: 5rem;
  padding: 5rem 2rem;
  width: 100vw;

}
.myself__p{
  color: white;
  font-size: 3rem;
  max-width: 70%;
  line-height: 5rem;
  text-align: justify;
  margin: 0 auto;
  font-family: sans-serif;
}

p ol img{
  display: flex;
  border-radius: 1rem;
  margin: 4rem auto;
  width: 20vw;
}
.container__technologies{
  display: grid;
  grid-template-columns: 1fr;
}
@media  (min-width: 320px) {
.my-name{
    font-size: 2rem;
    margin:  2rem;
    padding: 0; 
    height: auto;
}
.separador{
    width: 100%;
    height: 25vh;
    min-height:19.3rem ;
  }
  .navbar__li , a{
    font-size:1.8rem;
}
.banner{
    height: 25vh;
    min-height:19.3rem ;
    -webkit-clip-path:none;
            clip-path:none;
    background-image: url(https://i.postimg.cc/fRTrzh6r/banner.png);
    background-repeat: round;
    z-index: -1;
    
  }
.myself__p{
    font-size: 2rem;
    line-height: 3rem;
    text-align: left;
}
.container__h1{
    font-size: 3rem;
}
.container__technologies{
    grid-template-columns: 1fr;
}
.profile__img{
    width: 20vw;
    margin-top: 1.5rem;
    margin-right: 5vw;
}
}
@media (min-width: 600px){
    .my-name{
        font-size: 3rem;
        padding-left: 2rem; 
    }
    .separador{
        width: 100%;
        height: 25vh;
        min-height:26.2rem ;
      }
      .navbar__li , a{
        font-size:2rem;
    }
    .banner{
        min-height:26.2rem ;
        -webkit-clip-path:none;
                clip-path:none;
        background-image: url(https://i.postimg.cc/fRTrzh6r/banner.png);
        background-repeat: round;
        z-index: -1;
      }
    
    .myself__p{
        font-size: 2rem;
        line-height: 3rem;
        text-align: justify;
        width: 100vw;
    }
    .container__h1{
        font-size: 3rem;
    
    }
    .profile__img{  
            width:18vw;
            margin-top: 1.5rem;
            margin-right: 5vw;
    } 
    .container__technologies{
        grid-template-columns: 1fr 1fr;
    }
}
@media  (min-width: 700px){
    .my-name{
        font-size: 4rem;
        padding-left: 2rem; 
    }
    .separador{
        width: 100%;
        height: 35vh;
        min-height:32.2rem;
      }
      .navbar__li , a{
        font-size:3rem;
    }
    .banner{
        height: 35vh;
        min-height:32.2rem;
        background-image: url(https://i.postimg.cc/fRTrzh6r/banner.png);
        background-repeat: round;
        -webkit-clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
                clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
        z-index: -1;
      }
  
    .myself__p{
        font-size: 2rem;
        line-height: 3rem;
        text-align: justify;
        width: 100vw;
    }
    .container__h1{
        font-size: 3rem;
        width: auto;
    }
    .profile__img{
        width: 20vw;
        margin-top: 4rem;
        margin-right: 5vw;
    }
    .recuadro{
        max-width: 33vw;
    }
    .container__technologies{
        grid-template-columns: 1fr 1fr;
    }
}
@media (min-width: 960px){
    .my-name{
        font-size:6rem;
        padding-left: 5vw; 
    }
    .separador{
        height: 50vh;
        min-height:42.7rem;
    }
    .banner{
        height: 50vh;
        min-height:42.5rem;
    }
     
    .myself__p{
        font-size: 3rem;
        line-height: 4.5rem;
        text-align: justify;
        width: 100vw;
    }
    .container__h1{
        font-size: 6rem;
        width: auto;
    }
    .profile__img{
        width: 18vw;
        margin-top: 6rem;
        margin-right: 5vw;
        margin-bottom: 0px;
    }
    .container__technologies{
        grid-template-columns: 1fr 1fr 1fr;
    }
}
