@media (min-width: 600px){
    .my-name{
        font-size: 3rem;
        padding-left: 2rem; 
    }
    .separador{
        width: 100%;
        height: 25vh;
        min-height:26.2rem ;
      }
      .navbar__li , a{
        font-size:2rem;
    }
    .banner{
        min-height:26.2rem ;
        clip-path:none;
        background-image: url(https://i.postimg.cc/fRTrzh6r/banner.png);
        background-repeat: round;
        z-index: -1;
      }
    
    .myself__p{
        font-size: 2rem;
        line-height: 3rem;
        text-align: justify;
        width: 100vw;
    }
    .container__h1{
        font-size: 3rem;
    
    }
    .profile__img{  
            width:18vw;
            margin-top: 1.5rem;
            margin-right: 5vw;
    } 
    .container__technologies{
        grid-template-columns: 1fr 1fr;
    }
}